<template>
  <div class="learn mescroll">
    <div class="top">
      <div class="headPic">
        <img :src="headImg?$utils.dealImg(headImg):require('../../assets/img/default/userDefault.png')" alt />
      </div>
      <p class="content" @click="tap('1')">
        今日学习
        <span>{{studyData[0]}}</span>分钟
      </p>
      <div class="labels">
        <div class="label" v-for="(item,index) in 3" :key="index" @click="tap(item)">
          <p class="num">
            {{studyData[index + 1]}}
            <span>{{index == 0?'分钟':index==1?'天':'节'}}</span>
          </p>
          <p class="text">累计学习</p>
        </div>
      </div>
    </div>
    <div class="mid">
      <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :border="false" :title-active-color="'#000'" :sticky="true">
        <van-tab title="我的课程">
          <div class="LearnPage">
            <div class="list" v-if="LearnPage.length">
              <div class="item" v-for="(item,index) in LearnPage" :key="index">
                <div class="left">
                  <img :src="item.aimg?$utils.dealImg(item.aimg):require('@/assets/img/default/defaultH.png')" alt />
                </div>
                <div class="right">
                  <p class="title">{{item.aname}}</p>
                  <div class="bottom">
                    <div class="text" @click="goDetail(item.acode)">去学习</div>
                  </div>
                </div>
              </div>
            </div>
            <div v-else class="nothing">
              <img src="../../assets/img/nodata.png" alt="">
              <p>暂无数据</p>
            </div>
          </div>
        </van-tab>
        <van-tab title="我的订单">
          <div class="OrdersPage">
            <div class="list" v-if="OrdersPage.length">
              <div class="item" v-for="(item,index) in OrdersPage" :key="index">
                <div class="item-top flex">
                  <div class="No flex-2">订单编号:{{item.orderno}}</div>
                  <div class="status" :style="item.orderStatus == 1 || item.orderStatus == 2?'color:#eb5252':item.orderStatus == 3?'color:#333':'color:#999'">{{item.orderStatus== 1?'待付款':item.orderStatus == 2?'待学习':'已取消'}}</div>
                </div>
                <div>
                  <div class="item-goods">
                    <div class="shopName">{{item.learnName}}</div>
                    <div class="num">×1</div>
                    <div class="price">￥{{item.oorderMoneyShifu.toFixed(2)||'0.00'}}</div>
                  </div>
                </div>
                <div class="orderInfo">
                  <div class="price">共1件课程 总计：¥{{item.oorderMoneyShifu.toFixed(2)}}</div>
                </div>
                <div class="item-bottom">
                  <div class="btn" @click="goDetail(item.learnCode)">去学习</div>
                </div>
              </div>
            </div>
            <div v-else class="nothing">
              <img src="../../assets/img/nodata.png" alt="">
              <p>暂无数据</p>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import { getcollegeLearn } from "@/api";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      classifyList: [
        {
          cateName: "我的课程"
        },
        {
          cateName: "我的订单"
        }
      ],
      LearnPage: [],
      OrdersPage: [],
      studyData: [],
      headImg: ""
    };
  },
  methods: {
    tap(type) {
      switch (type) {
        case "1":
          this.$router.push({
            name: "learningRecord"
          });
          break;

        default:
          break;
      }
    },
    goDetail(acode) {
      this.$router.push({
        name: "courseDetail",
        query: {
          acode: acode
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getcollegeLearn().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.LearnPage = res.data.LearnPage;
          this.OrdersPage = res.data.OrdersPage;
          this.studyData = res.data.studyData;
          this.headImg = res.data.user.userHeader;
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      });
    }
  },
  mounted() {
    document.title = "学习中心";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.learn {
  height: calc(100vh - 50px);
  .top {
    background: url("../../assets/img/college/bg.png") no-repeat;
    background-size: 100% 100%;
    padding: 20px 10px;
    .headPic {
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
    }
    .content {
      font-size: 14px;
      font-weight: bold;
      padding: 10px 0;
      span {
        font-size: 18px;
        color: #f33039;
      }
    }
    .labels {
      display: flex;
      align-items: center;
      .label {
        flex: 1;
        text-align: center;
        .num {
          font-weight: bold;
          line-height: 30px;
          font-size: 18px;
          span {
            font-size: 12px;
            font-weight: normal;
            color: #999;
          }
        }
        .text {
          font-size: 12px;
          line-height: 25px;
        }
      }
    }
  }
  .mid {
    background: #fff;
    padding: 10px 0 0;
    .LearnPage {
      .list {
        background: #fafafa;
        padding: 0 0 10px;
        .item {
          border-bottom: 1pt solid #efefef;
          margin-bottom: 10px;
          padding: 5px 10px;
          display: flex;
          background: #fff;
          .left {
            img {
              width: 120px;
              margin-right: 5px;
            }
          }
          .right {
            position: relative;
            flex: 2;
            .title {
              font-size: 13px;
              line-height: 20px;
              height: 40px;
              max-height: 50px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .bottom {
              text-align: right;
              .text {
                line-height: 30px;
                display: inline-block;
                width: 70px;
                border-radius: 30px;
                text-align: center;
                color: #ca4540;
                font-size: 13px;
                background: #f6f1e7;
              }
            }
          }
        }
      }
    }
    .OrdersPage {
      .list {
        background: #fafafa;
        padding: 0 0 10px;
        .item {
          padding: 15px 10px;
          border-bottom: 1pt solid #efefef;
          background: #fff;
          .flex {
            display: flex;
            .flex-1 {
              flex: 1;
            }
            .flex-2 {
              flex: 2;
              position: relative;
            }
          }
          .item-top {
            font-size: 15px;
            line-height: 20px;
            .No {
              color: #333;
            }
          }
          .item-goods {
            display: flex;
            align-items: center;
            font-size: 13px;
            line-height: 20px;
            padding: 8px 0;
            .shopName {
              flex: 2;
            }
            .num {
              width: 30px;
              text-align: right;
              color: #999;
              margin-left: 10px;
            }
            .price {
              width: 70px;
              text-align: right;
              margin-left: 10px;
            }
          }
          .orderInfo {
            font-size: 15px;
            .price {
              font-size: 14px;
              text-align: right;
              color: #666;
            }
          }
          .item-bottom {
            text-align: center;
            overflow: hidden;
            padding: 15px 0;
            .btn {
              width: 80px;
              color: #666;
              border: 1pt solid #e5e5e5;
              float: right;
              height: 30px;
              line-height: 30px;
              border-radius: 5px;
              font-size: 13px;
              margin-left: 5px;
            }
            .btnPay {
              background: #fff;
              color: $base-color;
              border: 1pt solid $base-color;
            }
          }
        }
      }
    }
    .nothing {
      text-align: center;
      padding: 40px 0;
      img {
        width: 150px;
      }
      p {
        font-size: 13px;
        color: #666;
        line-height: 30px;
        margin: 10px 0 0;
      }
    }
  }
}
</style>
<style lang="scss">
.learn {
  .van-tabs__wrap {
    border-bottom: 1pt solid #e0e0e0;
  }
}
</style>
